body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.leftContainer {
  float: left;
  line-height: 1.15;
  width: 24.25%;
  height: 100%;
  position: absolute;
  background-image: linear-gradient(#f0f2f0, #000c40);
  font-weight: bold;
  overflow-x: auto;
}

#generate {
  text-align: center;
  margin-left: 2px;
}

#content {
  border-radius: 10px;
  height: auto;
  overflow: auto;
  padding-right: 0px;
  margin-right: 0px;
}

#loading-icon {
    display: block;
    margin-left: auto;
    margin-right: auto;
}
  
.header {
  /*  background-image: linear-gradient(#6190e8, #a7bfe8);*/
  /*border: 1px solid red;*/
  /*width: 100%;*/
  margin-left: 1%;
  margin-right: 1%;
  border-radius: 10px;
}

#contactInfo {
  -webkit-animation: contactInfo 1s 2.25s forwards;
  animation: contactInfo 1s 2.25s forwards;
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
  color: white;
  text-align: center;
  margin-right: 1%;    
  margin-bottom: 1%;
  margin-top: 70%;
  opacity: 0;
}

#contactInfo nav li {
  color: white;
  display: inline-block;
  top: 0;
  width: 4.35em;
  -webkit-animation: icon 1s ease-in-out forwards;
  animation: icon 1s ease-in-out forwards;
  margin-left: -30px;
}
#contactInfo nav li:nth-child(1) {
  -webkit-animation-delay: 2.5s;
  animation-delay: 2.5s;
}

#contactInfo nav li:nth-child(2) {
  -webkit-animation-delay: 3s;
  animation-delay: 3s;
}

#contactInfo nav li:nth-child(3) {
  -webkit-animation-delay: 3.5s;
  animation-delay: 3.5s;
}

#contactInfo nav li:nth-child(4) {
  -webkit-animation-delay: 4s;
  animation-delay: 4s;
}

#contactInfo nav a {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  border: 0;
  display: inline-block;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  display: inline-block;
  transition: all 0.2s ease-in-out;
  border-radius: 100%;
  text-align: center;
  color: white;
}

#contactInfo nav a:before {
  /*-moz-transition: all 0.2s ease-in-out;
  -webkit-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;*/
  border-radius: 100%;
  display: block;
  font-size: 1.75em;
  height: 2.5em;
  line-height: 2.5em;
  position: relative;
  text-align: center;
  top: 0;
  width: 2.5em;
}

#contactInfo nav a:hover {
  font-size: 1.1em;
}

/*#contactInfo nav a:hover:before {
  background-color: rgba(255, 255, 255, 0.175);
  color: #fff;
}

#contactInfo nav a:active {
  font-size: 0.95em;
  background: none;
}

#contactInfo nav a:active:before {
  background-color: rgba(255, 255, 255, 0.35);
  color: #fff;
}*/

#contactInfo h1 p {
  color: white;
  font-family: "Raleway", sans-serif;
  font-size: 62px;
  font-weight: 800;
  line-height: 72px;
  margin: 0 0 0px;
  text-align: center;
}

#contactInfo p {
  font-size: 18px;
  font-weight: 500;
  line-height: 32px;
}

#contactInfo nav a span {
  display: none;
}

@keyframes contactInfo {
  0% {
    -webkit-transform: translate3d(0, 2em, 0);
    transform: translate3d(0, 2em, 0);
    opacity: 0;
  }

  100% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

@-webkit-keyframes contactInfo {
  0% {
    -webkit-transform: translate3d(0, 2em, 0);
    transform: translate3d(0, 2em, 0);
    opacity: 0;
  }

  100% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

