.singleResult {
  width: 100%;
}

.methodResult {
  flex-grow: 1;
}

a {
  float: left;
  width: 50%;
}

#deleteButton {
  float: right;
  margin-left: 0%;
  margin-right: 0%;
  align-self: stretch;
}

.resultWrapper {
  width: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  float: left;
  width: 97%;
  text-align: center;
}

#resultValues {
  text-align: center;
  width: 100%;
}

#resultValues p {
  padding: 0;
  font-weight: bold;
  margin-bottom: 0;
  font-weight: normal;
  float: none;
  text-align: center;
}

#mainCanvas {
  float: right;
  width: 75%;
  height: 100%;
  position: absolute;
  margin-left: 24.25%;
}

#graph {
  height: 75%;
  width: 100%;
}

#results {
  width: 100%;
  height: 25%;
  overflow-x: auto;
  border-radius: 5px;
  background-image: linear-gradient(#f0f2f0, #000c40);
}

.list-group {
  width: 100%;
}

.list-group-item {
  width: 100%;
  padding: 0;
}

.list-group-item {
  line-height: 1.15;
}

